@import url(https://fonts.googleapis.com/css2?family=Hahmlet:wght@100;200;300;400;500;600;700;800;900&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Hahmlet", serif;
}
body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.faqs {
  width: 100%;
  max-width: 768px;
}
.faqs .faq {
  margin: 15px 0 15px 0;
  padding: 2px;
  background: rgb(248, 247, 247);
  border-radius: 8px;

  /* box-shadow: 0px 0px 7px rgb(0, 0, 0, 0.1); */
}
.faqs .faq-question {
  position: relative;
  font-size: 20px;
  padding-right: 80px;
  transition: all 0.4s ease;
}
.faqs .faq-question::after {
  content: "";
  position: absolute;
  top: 50%;
  right: 2px;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  width: 20px;
  height: 35px;
  background-image: url(/static/media/greenArrow.72167904.jpeg);
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
}
.faqs .faq-answer {
  opacity: 0;
  max-height: 0;
  overflow-y: hidden;
  transition: all 0.4s ease;
}
.faqs .faq.open .faq-question {
  margin-bottom: 15px;
}
.faqs .faq.open .faq-question::after {
  -webkit-transform: translateY(-50%) rotate(180deg);
          transform: translateY(-50%) rotate(180deg);
}
.faqs .faq.open .faq-answer {
  max-height: 1000px;
  opacity: 1;
}
@media screen and (max-width: 600px) {
  .faqs .faq-question {
    font-size: 14px;
    padding-right: 10px;
  }
}
@media screen and (max-width: 414px) {
  .faqs .faq-question {
    font-size: 12px;
    padding-right: 13px;
  }
}

footer {
  margin-top: 100px;
  position: relative;
}
.preview {
  width: 100%;
  padding: 0 30px 30px 30px;
  margin-top: 30px;
  background-image: linear-gradient(
    to right bottom,
    rgba(62, 177, 41, 0.2),
    rgba(145, 211, 188, 0.1)
  );
}
.footer_section {
  background: #3b3b3b;
  padding: 30px 30px 30px 30px;
  color: #fff;
  display: flex;
  justify-content: space-between;
}
.preview p {
  width: 60%;
  padding: 40px 0px 0 0;
}
.circle {
  position: absolute;
  right: 30px;
  top: 30px;
}
.footer_nav ul li {
  list-style: none;
}
.footer_nav ul li a {
  text-decoration: none;
  color: #fff;
}
.rajiss-ctrl p img {
  width: 40px;
  background: #fff;
  border-radius: 5px;
  z-index: 4;
}
.social img {
  width: 40px;
  border-radius: 10px;
  margin: 10px 10px 0 0;
}
fieldset {
  padding: 10px;
  position: relative;
}
fieldset img {
  position: absolute;
  top: -7px;
  right: 10px;
  width: 40px;
  border-radius: 5px;
}
@media screen and (max-width: 768px) {
  .footer_remark {
    width: 55%;
  }
  .circle {
    top: 15px;
  }
}
@media screen and (max-width: 600px) {
  .preview p {
    width: 100%;
    padding: 20px 0 20px 0;
  }
  .circle {
    top: 25px;
  }
  .footer_section {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
  }
  .footer_nav {
    text-align: center;
    margin-bottom: 20px;
  }
  .rajiss-ctrl {
    text-align: center;
  }
}
@media screen and (max-width: 540px) {
  footer {
    font-size: 13px;
  }
  .footer_nav,
  .footer_remark {
    width: 100%;
  }
  .circle {
    display: none;
  }
  fieldset {
    width: 100%;
  }
}
@media screen and (max-width: 280px) {
  fieldset img {
    width: 34px;
  }
}

.header_container {
  min-height: calc(100vh - 60px);
  background-image: linear-gradient(
      to right bottom,
      rgba(62, 177, 41, 0.8),
      rgba(145, 211, 188, 0.8)
    ),
    url(/static/media/engrbtn.5c1665ac.jpeg);
  -webkit-clip-path: polygon(0 0, 100% 0, 100% 75vh, 0 100%);
          clip-path: polygon(0 0, 100% 0, 100% 75vh, 0 100%);
  background-size: cover;
  background-position: top;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.header_container .herder_preview {
  width: 70%;
  text-align: center;
}
.header_container .herder_preview h1 {
  margin-bottom: 10px;
  color: transparent;
  -webkit-text-stroke: 2px #fff;
  font-size: clamp(25px, 5vw, 50px);
  font-weight: 900;
}
.header_container .herder_preview p {
  margin-bottom: 30px;
  /* margin-top: -10px; */
  font-size: clamp(14px, 5vw, 25px);
  color: #fff;
  font-weight: 600;
  letter-spacing: 12px;
}
.header_container .herder_preview a {
  text-decoration: none;
  margin-top: 0;
  text-align: center;
  background: rgb(141, 230, 7);
  padding: 10px 40px;
  border-radius: 25px;
  font-size: 25px;
  color: #fff;
}

.phone img {
  position: relative;
  top: 8px;
  width: 30px;
  border-radius: 15px;
}
.phone a {
  text-decoration: none;
  color: #fff;
}
@media screen and (max-width: 768px) {
  .phone p {
    position: absolute;
    top: 15px;
    left: 15px;
    color: #fff;
    text-decoration: none;
  }
}
@media screen and (max-width: 600px) {
  .header_container {
    min-height: calc(100vh - 140px);
  }
}
@media screen and (max-width: 400px) {
  .header_container .herder_preview a {
    text-decoration: none;
