@media (min-width: 800px) {
  .nav-bar {
    margin: 0;
    width: 100%;
    height: 60px;
    padding: 0 50px;
    position: absolute;
    z-index: 1;
    top: 0;
    left: 0;
    /* background: rgb(149, 235, 241, 0.3); */
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .nav-bar .list-wrapper img {
    display: none;
  }
  nav ul {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  nav ul li {
    list-style: none;
    margin-left: 10px;
  }

  nav ul li a {
    margin: 0;
    text-align: center;
    text-decoration: none;
    color: #fff;
    font-weight: 500;
    font-size: 16px;
    border-radius: 20px;
    padding: 8px 12px;
  }
