.form_container {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: 60px;
}
.form_container form {
  padding: 20px 0 0 0;
  background: linear-gradient(rgba(62, 177, 41, 0.8), rgba(145, 211, 188, 0.8));
  width: 50%;
  margin-top: 30px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-radius: 15px;
}
.form_container h1 {
  margin-top: 50px;
  width: 35%;
  color: #3d3c3c;
  text-align: center;
}

.form_container form input.first-name,
.form_container form input.last-name,
.form_container form input.email {
  background: #fff;
  outline: none;
  border: none;
  font-size: 16px;
  width: 90%;
  padding: 10px;
  margin: 15px;
  position: relative;
  box-shadow: 0px 4px 8px 0px rgb(255, 255, 255),
    0px 6px 20px 0px rgb(0, 0, 0, 0.19);
}
.name-icon,
.name-icon2,
.mail-icon {
  text-align: center;
  width: 100%;
  position: relative;
}
.name-icon img,
.name-icon2 img,
.mail-icon img {
  position: absolute;
  width: 30px;
  z-index: 1;
  top: 20px;
  right: 50px;
}
.form_container form textarea {
  margin-top: 20px;
  outline: none;
  border: none;
  font-size: 16px;
  width: 90%;
  min-height: 100px;
  box-shadow: 0px 4px 8px 0px rgb(255, 255, 255),
    0px 6px 20px 0px rgb(0, 0, 0, 0.19);
}

.form_container form button {
  width: 150px;
  padding: 10px;
  outline: none;
  background: #3d3c3c;
  color: #fff;
  font-size: 16px;
  border-radius: 5px;
  cursor: pointer;
  margin: 20px;
  border: none;
  box-shadow: 0px 4px 8px 0px rgb(255, 255, 255),
    0px 6px 20px 0px rgb(0, 0, 0, 0.19);
  transition: all 0.4s ease;
}
.form_container form button:hover {
  background: #76c276;
}

@media screen and (max-width: 768px) {
  .form_container form {
    width: 70%;
  }
  .form_container h1 {
    margin-top: 30px;
    width: 90%;
    font-size: 20px;
  }
}
@media screen and (max-width: 600px) {
  .form_container form {
    width: 90%;
  }
  .name-icon img,
  .name-icon2 img,
  .mail-icon img {
    right: 28px;
  }
}
@media screen and (max-width: 414px) {
  .name-icon img,
  .name-icon2 img,
  .mail-icon img {
    right: 20px;
  }
}

